}

/*--------------------------------------------------------------
## Menus
--------------------------------------------------------------*/
.menu-toggle {
	width: 40px;
	height: 40px;
	margin: 0;
	padding: 0;
	border: none;
	outline: none;
	background-color: transparent;
}

.menu-toggle svg {
	width: 100%;
	height: 100%;
	fill: #604291;
}

.nav-menu {
	position: fixed;
	top: 100px;
	left: 100%;
	width: 100%;
	height: calc(100% - 100px);
	margin: 0;
	padding: 30px;
	background-color: white;
	list-style: none;
	transition: all 0.2s cubic-bezier(0.4, 0, 1, 1);
}

.menu-item {
	width: 100%;
	padding: 20px 0;
	border-bottom: 1px solid #e4e4e4;
}

.menu-item a {
	color: #604291;
	font-weight: bold;
	font-size: 1.375em;
	font-style: normal;
	text-decoration: none;
}

.main-navigation.toggled .nav-menu {
	left: 0;
}

@media screen and (min-width: 800px) {
	.menu-toggle {
		display: none;
	}
	.main-navigation {
		width: 100%;
	}
	.nav-menu {
		position: relative;
		top: 0;
		left: 0;
		padding: 0;
		background-color: transparent;
		text-align: right;
	}
	.nav-menu .menu-item {
		display: inline-block;
		width: auto;
		padding: 0 0 0 25px;
		border: none;
	}
	.nav-menu .menu-item a {
		color: white;
		font-size: 1.125em;
		text-transform: uppercase;
	}
}

.blog-post-pagination {
	margin-top: 85px;
}

.nav-links {
	display: flex;
	align-items: center;
	justify-content: center;
}

.page-numbers {
	display: block;
	margin: 0 15px;
	width: 30px;
	height: 30px;
	border-radius: 50%;
	background-color: #333333;
	color: #f7f7f7;
	font-size: 16px;
	font-weight: 300;
	text-decoration: none;
	text-align: center;
	line-height: 30px;
}

.page-numbers.current {
	background-color: #a7a7a7;
	cursor: not-allowed;
}

/*--------------------------------------------------------------
# Accessibility
--------------------------------------------------------------*/
/* Text meant only for screen readers. */
.screen-reader-text {
	border: 0;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	height: 1px;
	margin: -1px;
	overflow: hidden;
	padding: 0;
	position: absolute !important;
	width: 1px;
	word-wrap: normal !important;
	/* Many screen reader and browser combinations announce broken words as they would appear visually. */
}

.screen-reader-text:focus {
	background-color: #f1f1f1;
	border-radius: 3px;
	box-shadow: 0 0 2px 2px rgba(0, 0, 0, 0.6);
	clip: auto !important;
	clip-path: none;
	color: #21759b;
	display: block;
	font-size: 14px;
	font-size: 0.875rem;
	font-weight: bold;
	height: auto;
	left: 5px;
	line-height: normal;
	padding: 15px 23px 14px;
	text-decoration: none;
	top: 5px;
	width: auto;
	z-index: 100000;
	/* Above WP toolbar. */
}

/* Do not show the outline on the skip link target. */
#content[tabindex="-1"]:focus {
	outline: 0;
}

/*--------------------------------------------------------------
# Alignments
--------------------------------------------------------------*/
.alignleft {
	display: inline;
	float: left;
	margin-right: 1.5em;
}

.alignright {
	display: inline;
	float: right;
	margin-left: 1.5em;
}

.aligncenter {
	clear: both;
	display: block;
	margin-left: auto;
	margin-right: auto;
}

/*--------------------------------------------------------------
# Clearings
--------------------------------------------------------------*/
.clear:before,
.clear:after,
.entry-content:before,
.entry-content:after,
.comment-content:before,
.comment-content:after,
.site-header:before,
.site-header:after,
.site-content:before,
.site-content:after,
.site-footer:before,
.site-footer:after {
	content: "";
	display: table;
	table-layout: fixed;
}

.clear:after,
.entry-content:after,
.comment-content:after,
.site-header:after,
.site-content:after,
.site-footer:after {
	clear: both;
}

.post-card {
	width: 100%;
	margin-bottom: 40px;
	padding: 35px;
	background-color: white;
	box-shadow: 0px 0px 47.5px 2.5px rgba(0, 0, 0, 0.1);
}

.postHeader--thumbnail {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
}

.postHeader--thumbnail .thumbnail-container {
	width: 100%;
}

.postHeader--thumbnail .thumbnail-container img {
	display: block;
	width: 100%;
}

.postHeader--thumbnail .postContent--thumbnail {
	width: 100%;
	padding-top: 25px;
}

.entry-title,
.entry-title a {
	max-width: 735px;
	margin: 0 0 25px 0;
	font-size: 1.375em;
	font-family: "Roboto";
	color: #604291;
	font-weight: bold;
	text-decoration: none;
	transition: all .2s;
}

.entry-title a:hover {
	color: #8479c5;
}

.entry-content__card p {
	margin: 0;
}

.entry-footer {
	margin-top: 35px;
}

@media screen and (min-width: 800px) {
	.post-card {
		padding: 35px 70px;
	}
	.postHeader--thumbnail {
		flex-direction: row;
		align-items: center;
	}
	.postHeader--thumbnail .thumbnail-container {
		width: 330px;
		height: 330px;
	}
	.postHeader--thumbnail .thumbnail-container img {
		width: 100%;
	}
	.postHeader--thumbnail .postContent--thumbnail {
		width: calc(100% - 330px);
		padding: 0 0 0 25px;
	}
}

.article-single {
	position: relative;
	width: 100%;
	max-width: 800px;
	margin-top: 85px;
}

.article-single .entry-title {
	font-size: 30px;
}

.article-single .entry-content {
	margin: 0 0 35px 0;
}

.article-single .entry-content p,
.article-single .entry-content h1,
.article-single .entry-content h2,
.article-single .entry-content h3 {
	margin-top: 0;
}

.article-single .entry-content h2,
.article-single .entry-content h3 {
	font-size: 25px;
	color: #8479c5;
}

.article-single .entry-content h3 {
	font-size: 20px;
}

.article-single .entry-content p {
	line-height: 30px;
	font-size: 18px;
	font-weight: normal;
	color: #333333;
}

.comment-form .comment-notes {
	display: none;
}

.comment-form p {
	margin: 0;
}

.comment-form label {
	display: block;
	margin: 15px 0;
	font-size: 20px;
	font-weight: bold;
	color: #333333;
}

.comment-form input {
	width: 100%;
	height: 50px;
}

.comment-form input,
.comment-form textarea {
	padding: 0 10px;
	border: 1px solid #d6d6d6;
	outline: none;
}

.comment-form textarea {
	padding-top: 5px;
}

.comment-list {
	margin: 0;
	padding: 0;
	border-bottom: 1px solid #e3e3e3;
}

.comment-list, .comment-list ul {
	list-style: none;
}

.comment-list .comment {
	padding: 30px 0;
	border-top: 1px solid #e3e3e3;
}

.comment-list .comment p {
	font-size: 20px;
	color: gray;
	transition: all .2s;
}

.comment-list .comment:hover p {
	color: #333333;
}

.comment-list .comment .comment-data {
	display: flex;
}

.comment-list .comment .comment-avatar {
	width: 50px;
	height: 50px;
	margin-right: 25px;
	overflow: hidden;
	border-radius: 50%;
}

.comment-list .comment .comment-avatar img {
	display: block;
	width: 100%;
}

.comment-list .comment .comment-metadata {
	font-size: 13px;
	font-weight: 300;
	font-style: italic;
	color: gray;
}

.comment-list .comment .comment-metadata .__name {
	padding-right: 10px;
	font-size: 18px;
	font-style: normal;
	font-weight: bold;
	color: #8479c5;
}

.btn-primary {
	display: inline-block;
	padding: 0 60px;
	height: 55px;
	background-color: #8479c5;
	color: white;
	text-align: center;
	line-height: 55px;
	text-transform: uppercase;
	text-decoration: none;
	font-size: 1.125em;
	transition: all .2s;
	cursor: pointer;
}

.btn-primary:hover {
	background-color: #604291;
}

/*--------------------------------------------------------------
# Widgets
--------------------------------------------------------------*/
.widget {
	margin: 0 0 1.5em;
	/* Make sure select elements fit in widgets. */
}

.widget select {
	max-width: 100%;
}

/*--------------------------------------------------------------
# Content
--------------------------------------------------------------*/
/*--------------------------------------------------------------
## Posts and pages
--------------------------------------------------------------*/
.sticky {
	display: block;
}

.updated:not(.published) {
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}

/*--------------------------------------------------------------
## Comments
--------------------------------------------------------------*/
.comment-content a {
	word-wrap: break-word;
}

.bypostauthor {
	display: block;
}

/*--------------------------------------------------------------
# Infinite scroll
--------------------------------------------------------------*/
/* Globally hidden elements when Infinite Scroll is supported and in use. */
.infinite-scroll .posts-navigation,
.infinite-scroll.neverending .site-footer {
	/* Theme Footer (when set to scrolling) */
	display: none;
}

/* When Infinite Scroll has reached its end we need to re-display elements that were hidden (via .neverending) before. */
.infinity-end.neverending .site-footer {
	display: block;
}

/*--------------------------------------------------------------
# Media
--------------------------------------------------------------*/
.page-content .wp-smiley,
.entry-content .wp-smiley,
.comment-content .wp-smiley {
	border: none;
	margin-bottom: 0;
	margin-top: 0;
	padding: 0;
}

/* Make sure embeds and iframes fit their containers. */
embed,
iframe,
object {
	max-width: 100%;
}

/* Make sure logo link wraps around logo image. */
.custom-logo-link {
	display: inline-block;
}

/*--------------------------------------------------------------
## Captions
--------------------------------------------------------------*/
.wp-caption {
	margin-bottom: 1.5em;
	max-width: 100%;
}

.wp-caption img[class*="wp-image-"] {
	display: block;
	margin-left: auto;
	margin-right: auto;
}

.wp-caption .wp-caption-text {
	margin: 0.8075em 0;
}

.wp-caption-text {
	text-align: center;
}

/*--------------------------------------------------------------
## Galleries
--------------------------------------------------------------*/
.gallery {
	margin-bottom: 1.5em;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
}

.gallery-columns-2 .gallery-item {
	max-width: 50%;
}

.gallery-columns-3 .gallery-item {
	max-width: 33.33%;
}

.gallery-columns-4 .gallery-item {
	max-width: 25%;
}

.gallery-columns-5 .gallery-item {
	max-width: 20%;
}

.gallery-columns-6 .gallery-item {
	max-width: 16.66%;
}

.gallery-columns-7 .gallery-item {
	max-width: 14.28%;
}

.gallery-columns-8 .gallery-item {
	max-width: 12.5%;
}

.gallery-columns-9 .gallery-item {
	max-width: 11.11%;
}

.gallery-caption {
	display: block;
}

.site-main,
.site-container {
	position: relative;
	max-width: 1220px;
	padding: 0 25px;
	margin: 0 auto;
}

.site-main.moveTop,
.site-container.moveTop {
	margin-top: -95px;
	z-index: 3;
}

.site-header {
	position: relative;
	width: 100%;
}

.site-header.is-home {
	background-position: center;
	background-size: cover;
	background-repeat: no-repeat;
}

.site-header.is-home::after {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 1;
	width: 100%;
	height: 100%;
	background-image: linear-gradient(to right, rgba(96, 66, 145, 0.6) 0%, rgba(121, 155, 197, 0.85) 58%, #797fc5 100%);
}

.site-header_top {
	position: relative;
	z-index: 4;
	background-color: white;
	border-bottom: 1px solid #e3e3e3;
}

.site-header_top .site-main {
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 100px;
}

.site-logo-container {
	display: block;
	width: 35px;
	height: 35px;
}

.site-logo-container.__default {
	background-color: white;
	opacity: .5;
}

.site-header.is-home .site-header_top {
	background-color: transparent;
	border-bottom: none;
}

.site-header.is-home .site-title a {
	color: white;
}

.site-banner {
	position: relative;
	z-index: 3;
	min-height: 400px;
	padding-top: 85px;
	text-align: center;
}

.site-branding {
	color: white;
}

.site-title {
	margin: 0 0 20px 0;
	color: #f3f3f3;
	line-height: initial;
	font-style: normal;
	font-size: 4.375em;
	text-decoration: none;
}

.site-description {
	max-width: 750px;
	margin: 0 auto;
	font-size: 1.875em;
	font-weight: 300;
}

.categories {
	display: flex;
	justify-content: center;
	flex-wrap: wrap;
	width: 100%;
	padding: 45px 25px 180px 25px;
}

.categories .category {
	display: block;
	margin: 10px;
	padding: 5px 25px;
	border-radius: 293px;
	background-color: white;
	color: #604291;
	text-decoration: none;
	font-weight: bold;
	text-transform: uppercase;
	transition: all .2s;
}

.categories .category:hover, .categories .category.current_tag {
	background-color: #604291;
	color: white;
}

.site-footer {
	width: 100%;
	color: white;
	margin-top: 100px;
	padding: 85px 0;
	background-color: #333333;
}

.site-footer .site-container {
	display: flex;
	align-items: flex-start;
	justify-content: space-between;
	flex-direction: column;
}

.site-footer-section {
	width: 100%;
	max-width: 400px;
	margin: 0 auto;
	padding: 0;
	font-size: 1em;
}

.site-footer-section ._header {
	font-size: 1.5625em;
	color: white;
}

.site-footer-section ._about {
	color: #f5f5f5;
	font-weight: 300;
}

.footer-widget-title {
	font-weight: bold;
	font-size: 1.375em;
	color: white;
}

@media screen and (min-width: 800px) {
	.site-footer-section {
		width: 33%;
		padding: 0 20px;
	}
	.site-footer .site-container {
		flex-direction: row;
	}
}
